.hero-container {
    .hero {
        position: relative;
        height: 600px;
        background-image:
            linear-gradient(to left, rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.5)),
            url(../img/hero.jpg);
        background-size: cover;
        @media screen and (max-width: $bp-tablet) {
            height: 450px;
        }
    }
    .hero-content {
        position: absolute;
        top: 30%;
        left: 15%;
        max-width: 450px;
        @media screen and (max-width: $bp-tablet) {
            font-size: 0.8rem;
            left: 9%;
            top: 18%;
        }
        .brand-call {
            color: $orange-hover;
            font-size: 1.2rem;
            font-weight: bold;
            text-transform: uppercase;
        }
        h1 {
            color: $white;
            font-weight: $black;
            font-family: $secondary-font
        }
        p {
            color: $white;
        }
        .hero-contact {
            margin: 2rem 0;
            .fas {
                margin: 0 2rem 0 0;
            }
            a {
                height: 40px;
                font-weight: 600;
                font-size: 1.4rem;
                color: $white;
            }
        }
        button {
            background-color: $orange;
            text-transform: uppercase;
            font-weight: bold;
            letter-spacing: 0.2rem;
            cursor: pointer;
            border: none;
            color: $white;
            padding: 15px 32px;
            text-align: center;
            text-decoration: none;
            display: inline-block;
            font-size: 16px;
            &:hover {
                background-color: $orange-hover;
            }
        }
    }
}

.page-content {
    max-width: 1170px;
    margin: 5rem auto;
    @media screen and (max-width: $bp-tablet) {
        margin-top: 2rem;
    }
    .content-block {
        display: flex;
        height: 600px;
        margin-right: 2rem;
        @media screen and (max-width: $bp-large) {
            flex-direction: column-reverse;
            align-items: center;
            height: auto;
            margin: 0;
        }
        .welcome-montage {
            padding: 1rem;
            max-height: 600px;
            max-width: 50%;
            img {
                max-width: 100%;
                height: auto;
            }
        }
        .welcome-content {
            width: 50%;
            padding-left: 1rem;
            margin-top: -2rem;
            line-height: 1.4rem;
            @media screen and (max-width: $bp-large) {
                width: 700px;
            }
            @media screen and (max-width: $bp-tablet) {
                width: auto;
                margin: 0 1rem;
            }
            h2 {
                margin: 3rem 0 3rem 0;
            }
        }
    }
}

#why-choose {
    display: flex;
    flex-direction: column;
    align-items: center;
    p {
        text-align: center;
        width: 50%;
        @media screen and (max-width: $bp-tablet) {
            width: 90%;
        }
    }
    .grid {
        display: flex;
        flex-wrap: wrap;
        height: 400px;
        @media screen and (max-width: $bp-large) {
            height: auto;
        }
        .grid-item-container {
            display: flex;
            flex-direction: column;
            height: 50%;
            width: 25%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            @media screen and (max-width: $bp-large) {
                width: 50%;
            }
            .image-container {
                height: 70px;
                width: 100%;
                display: flex;
                justify-content: center;
                img {
                    height: auto;
                    max-width: 100%;
                }
            }
            h4 {
                text-align: center;
                width: 85%;
                font-weight: 600;
                margin-top: 0;
            }
        }
    }
}
